import React, { Component } from 'react'
import ReactSearchBox from 'react-search-box'

export class SearchPage extends Component {
    data = [
        {
            key: 'john',
            value: 'John Doe',
        },
        {
            key: 'jane',
            value: 'Jane Doe',
        },
        {
            key: 'mary',
            value: 'Mary Phillips',
        },
        {
            key: 'robert',
            value: 'Robert',
        },
        {
            key: 'karius',
            value: 'Karius',
        },
    ];

    render() {
        return (
            <ReactSearchBox
                placeholder="Search"
                value="Doe"
                data={this.data}
                callback={record => alert(record)}
                onSelect={record => console.log(record.value)}
            />
        )
    }
}